ACE;IACE,uBAAuB;EACzB;EACA;IACE,yBAAyB;EAC3B;AACF","sourcesContent":[":root {\r\n  --purple-400: #b1b2ff;\r\n  --purple-300: #aac4ff;\r\n  --purple-200: #d2daff;\r\n  --purple-100: #eef1ff;\r\n  --text: #7286d3;\r\n  --font-mont: \"Montserrat\";\r\n  --font-osw: \"Oswald\";\r\n}\r\n\r\n* {\r\n  padding: 0;\r\n  margin: 0;\r\n}\r\n\r\nbody {\r\n  font-family: \"Montserrat\", sans-serif;\r\n  background-color: var(--purple-100);\r\n  min-height: 100vh;\r\n  display: flex;\r\n  flex-direction: column;\r\n}\r\n\r\nh1,\r\nh3 {\r\n  font-family: var(--font-osw);\r\n}\r\n\r\ninput,\r\na,\r\nbutton {\r\n  min-height: 44px !important;\r\n  min-width: 44px !important;\r\n  height: fit-content;\r\n  width: fit-content;\r\n}\r\n\r\n.logo {\r\n  text-decoration: none;\r\n  color: var(--text);\r\n}\r\n\r\n.hidden {\r\n  display: none !important;\r\n}\r\n\r\n/*\r\n  AppBar\r\n*/\r\n\r\n.app-bar {\r\n  padding: 8px 16px;\r\n  background-color: var(--purple-100);\r\n  display: grid;\r\n  grid-template-columns: auto 1fr auto;\r\n  gap: 10px;\r\n  position: sticky;\r\n  top: 0;\r\n  z-index: 99;\r\n  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.2);\r\n  max-width: 100%;\r\n}\r\n\r\n.app-bar .app-bar__menu {\r\n  display: flex;\r\n  align-items: center;\r\n}\r\n\r\n.app-bar .app-bar__menu button {\r\n  background-color: transparent;\r\n  border: none;\r\n  font-size: 18px;\r\n  padding: 8px;\r\n  cursor: pointer;\r\n}\r\n\r\n.app-bar .app-bar__brand {\r\n  display: flex;\r\n  align-items: center;\r\n  column-gap: 16px;\r\n  justify-content: space-evenly;\r\n}\r\n\r\n.app-bar .app-bar__brand h1 {\r\n  color: var(--text);\r\n  text-transform: uppercase;\r\n  font-size: 22px;\r\n  user-select: none;\r\n  min-width: 44px !important;\r\n  min-height: 44px !important;\r\n  display: inline-block;\r\n}\r\n\r\n.app-bar .app-bar__brand a {\r\n  min-width: 44px !important;\r\n  min-height: 44px !important;\r\n  display: inline-block;\r\n}\r\n\r\n.app-bar .app-bar__navigation {\r\n  position: absolute;